AppContent {
	padding-top: 10px;
}

.row-image-title {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: baseline;
	    align-items: baseline;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.row-center {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.app-container {
	-ms-flex-direction: row;
	    flex-direction: row;
}

@media screen and (max-width: 615px) {
	.app-container {
		-ms-flex-direction: column;
		    flex-direction: column;
	}
	.options-container, .image-container {
		max-width: 100% !important;
	}
	.cornerstone-canvas {
		width: 100% !important;
	}

	.row-image-title {
		-ms-flex-direction: column;
		    flex-direction: column;
		margin-bottom: 10px;
	}

	.row-image-title h5 {
		font-size: 1rem;
	}

	.row-image-title .row-center {
		width: 100%;
	}
}




